<template>
  <div>
  <form class="login-form">
    <universal-input type="text" v-model="username" labelName="Username"></universal-input>
    <universal-input type="password" v-model="password" labelName="Password"></universal-input>
    <li v-if="error" class="error">{{error.message}}</li>
    <action-button name="Log in" :action="login"></action-button>
  </form>
  </div>
</template>

<script>
import UniversalInput from '../components/htmlBlocks/inputs/UniversalInput.vue'
import ActionButton from '../components/htmlBlocks/buttons/ActionButton.vue'
import axios from 'axios'

export default {
  name: 'login',
  components: {
    UniversalInput,
    ActionButton,
  },
  data: function () {
    return {
      username: 'ggg',
      password: 'gggg',
      error: '',
    }
  },
  methods: {
    login: function () {
      let username = this.username
      let password = this.password
      this.$store.dispatch('login', { username, password })
      .then(() => this.$router.push('/dashboard'))
      .catch(err => this.error = err.response.data)
    },
  }

}
</script>

<style>
.login-form {
  overflow: hidden;
  padding:0 15%;
  margin:5% 0;
}
.error {
  color:#666464;
  font-size: 12px;
  text-align: center;
}
</style>
